.wrapper {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: #6F0071;
}

.form {
    margin-top: 15px;
    width: 30%;
    background-color: white;
    border-radius: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.form__element {
    width: 80%;
    height: 10%;
    display: flex;
    flex-direction: column;
}

.form__input {
    width: 100%;
    height: 100px;
    border: 1px solid rgba(196, 196, 196, 0.3);
    border-radius: 15px;
    margin-top: 10px;
    padding-left: 15px;
    font-size: 18px;
}

.form__button {
    width: 50%;
    height: 50px;
    border-radius: 15px;
    font-size: 18px;
    color: white;
    background-color: #6F0071;
}

.title {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.title h1 {
    color: white;
}

.title img {
    width: 100px;
    height: 100px;
}

.service-container {
    width: 100%;
    height: 100vh;
    background-color: rgba(66,66,66,0.73);
    display: none;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
}

.service__modal {
    width: 400px;
    height: 400px;
    border-radius: 20px;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.service__element {
    width: 160px;
    height: 50px;
    border: 1px solid gray;
    border-radius: 15px;
    padding: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.service__element:last-child {
    margin-top: 40px;
}
